"use client"

import Link from "next/link"
import { ArrowLeft, Milk } from "lucide-react"
import { cn } from "@/lib/utils"

interface AuthLayoutProps {
  children: React.ReactNode
  title: string
  subtitle?: string
  icon?: React.ReactNode
  variant?: "customer" | "admin" | "delivery"
}

export function AuthLayout({ children, title, subtitle, icon, variant = "customer" }: AuthLayoutProps) {
  const bgGradient = variant === "admin" 
    ? "bg-gradient-to-br from-slate-100 to-slate-50" 
    : "bg-gradient-to-br from-emerald-50 to-[hsl(var(--background))]"

  return (
    <div className={cn("min-h-screen flex flex-col", bgGradient)}>
      {/* Background Pattern */}
      <div className="absolute inset-0 bg-[url('data:image/svg+xml;base64,')] opacity-50" />
      
      {/* Header */}
      <header className="relative p-4 sm:p-6">
        <Link
          href="/"
          className="inline-flex items-center gap-2 text-[hsl(var(--muted-foreground))] hover:text-[hsl(var(--foreground))] transition-colors text-sm"
        >
          <ArrowLeft className="w-4 h-4" />
          <span>Back to home</span>
        </Link>
      </header>

      {/* Main Content */}
      <main className="relative flex-1 flex items-center justify-center p-4 pb-12">
        <div className="w-full max-w-md">
          {/* Brand Header */}
          <div className="text-center mb-8">
            <div className={cn(
              "inline-flex items-center justify-center w-14 h-14 rounded-xl mb-4 shadow-lg",
              variant === "admin" ? "bg-slate-800" : "bg-[hsl(var(--primary))]"
            )}>
              {icon || <Milk className="w-7 h-7 text-white" />}
            </div>
            <h1 className="text-xl font-bold text-[hsl(var(--foreground))]">{title}</h1>
            {subtitle && (
              <p className="text-sm text-[hsl(var(--muted-foreground))] mt-1">{subtitle}</p>
            )}
          </div>

          {/* Card Content */}
          <div className="bg-[hsl(var(--card))] rounded-xl border border-[hsl(var(--border))] shadow-sm p-6">
            {children}
          </div>
        </div>
      </main>
    </div>
  )
}
