"use client"

import { cn } from "@/lib/utils"
import { LucideIcon } from "lucide-react"

interface EmptyStateProps {
  icon?: LucideIcon
  title: string
  description?: string
  action?: React.ReactNode
  className?: string
}

export function EmptyState({ icon: Icon, title, description, action, className }: EmptyStateProps) {
  return (
    <div className={cn("flex flex-col items-center justify-center py-12 px-4 text-center", className)}>
      {Icon && (
        <div className="w-12 h-12 rounded-full bg-[hsl(var(--muted))] flex items-center justify-center mb-4">
          <Icon className="w-6 h-6 text-[hsl(var(--muted-foreground))]" />
        </div>
      )}
      <h3 className="text-base font-semibold text-[hsl(var(--foreground))] mb-1">
        {title}
      </h3>
      {description && (
        <p className="text-sm text-[hsl(var(--muted-foreground))] max-w-sm mb-4">
          {description}
        </p>
      )}
      {action}
    </div>
  )
}
